.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}
.container {
  padding: 0 16px;
}
.main {
  flex: 1 1 100%;
  @include media-breakpoint-up(sm) {
    &_muted {
      background-color: var(--color-sections-bg, #F5F5F5);
    }
  }
}
.content {
  padding: 30px 0 0 0;
  display: flex;
  flex-direction: column;
}

.section {
  position: relative;
  margin-bottom: 72px;
  &_custom-background {
    padding: 60px 0;
    background-size: cover;
    background-position: center;
    & ~ .section_custom-background {
      margin-top: -72px;
    }
  }
  &_small-margin {
    margin-bottom: 60px;
  }
  &_small-padding {
    padding: 40px 0;
  }
  &__title {
    margin-top: 0;
    margin-bottom: 28px;
    display: block;
    span {
      margin-right: 1rem;
    }
  }
  &__more {
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }
  &__load {
    margin-top: 32px;
    justify-content: center;
    &-button {
      width: 100%;
    }
  }
  @include media-breakpoint-up(sm) {
    margin-bottom: 60px;
    &_custom-background {
      & ~ .section_custom-background {
        margin-top: -60px;
      }
    }
    &_no-margin {
      margin: 0 !important;
    }
    &_small-margin {
      margin-bottom: 40px;
    }
    &_padding {
      padding: 72px 0;
      & + .section_padding {
        margin-top: -132px;
      }
    }
    &_background-muted {
      background-color: var(--color-sections-bg, #F5F5F5)
    }
    &_background-white {
      background-color: #fff;
    }
  }
  @include media-breakpoint-up(lg) {
    &_padding {
      padding: 60px 0;
      & + .section_padding {
        margin-top: -120px;
      }
    }
  }
  @include media-breakpoint-down(md) {
    &__more {
      margin-top: 32px;
    }
  }
  @include media-breakpoint-down(xs) {
    &__more-button {
      width: 100%;
    }
    &__title {
      margin-bottom: 24px;
    }
  }
}

h1.section__title {
  margin-top: 0;
  margin-bottom: 32px;
  @include media-breakpoint-down(xs) {
    margin-bottom: 28px;
  }
}

.page-title {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  span {
    &:first-child {
      margin-right: 16px;
    }
    &:last-child {
      &:not(:first-child) {
        display: inline-block;
        font-weight: normal;
        font-size: 0.75rem;
        color: var(--color-text-muted, #767676);
        transform: translateY(4px);
      }
    }
  }
}

.section .section {
  &:last-child {
    margin-bottom: 0;
  }
}

.background-box {
  padding: 60px 0;
  background-color: #fff;
  margin: 0;
}

.page-container {
  @include media-breakpoint-up(sm) {
    background-color: #fff;
    padding: 30px 20px;
    border: 1px solid var(--color-table-border, #E5E5EA);
    border-radius: 2px;
  }
  @include media-breakpoint-up(md) {
    padding: 40px 30px;
  }
  @include media-breakpoint-up(lg) {
    padding: 40px;
  }
  @include media-breakpoint-up(xl) {
    padding: 40px 100px;
  }
}

.display-container {
  @include media-breakpoint-up(sm) {
    background-color: #fff;
    padding: 30px 20px;
    border: 1px solid var(--color-table-border, #E5E5EA);
    border-radius: 2px;
  }
  @include media-breakpoint-up(md) {
    padding: 40px 30px;
  }
  @include media-breakpoint-up(lg) {
    padding: 40px;
  }
  @include media-breakpoint-up(xl) {
    padding: 40px 50px;
  }
}


.grid {
  margin-bottom: -20px;
  &__block {
    margin-bottom: 20px;
  }
  &__head {
    margin-bottom: 35px;
  }
  @include media-breakpoint-down(xs) {
    margin-bottom: -24px;
    &__block {
      margin-bottom: 24px;
    }
  }
}