.logo {
  &__link {
    display: block;
    width: 200px;
  }
  img, svg {
    display: block;
    object-fit: contain;
    object-position: left center;
    margin: 0 !important;
    width: 100% !important;
    height: 100% !important;
  }
  svg {
    fill: var(--color-header-logo, #000);
  }
}

.aside {
  .advertising-preview {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.card-labels {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
}
.card-label {
  display: block;
  width: 50px;
  font-size: 0.75rem;
  color: #fff;
  line-height: 20px;
  text-align: center;
  font-weight: 700;
  position: relative;
  &_style-3 {
    background-color: var(--color-label-hit, #3EC28F);
  }
  &_style-2 {
    background-color: var(--color-label-sale, #FF9500);
  }
  &_style-1 {
    background-color: var(--color-label-new, #3EC28F);
  }
}

.category-card {
  font-size: 0.875rem;
  line-height: 1.25rem;
  &__container {
    min-height: 140px;
    color: #fff;
    border-radius: 2px;
    overflow: hidden;
    display: flex;
    align-items: flex-end;
    text-decoration: none;
    height: 100%;
    position: relative;
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.6);
      transition: background-color $trs1;
      z-index: 2;
    }
    .image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      &_contain {
        z-index: 3;
        opacity: 0.2;
        img {
          width: 45px;
          height: 45px;
          top: 30px;
          left: 30px;
        }
      }
    }
    &[data-touch], &[data-hover] {
      &:after {
        background-color: var(--color-category-block-hover, rgba(28, 82, 172, 0.75));
      }
    }
  } 
  &__title {
    padding: 20px 40px 20px 20px;
    width: 100%;
    z-index: 4;
    margin: 0;
    color: inherit;
    position: relative;
    font-size: inherit;
    line-height: inherit;
    .icon {
      position: absolute;
      right: 15px;
      width: 16px;
      height: 16px;
      bottom: 22px;
      transition: fill $trs1;
      fill: #fff;
    }
  }
  img {
    filter: grayscale(100%);
  }
  @include media-breakpoint-down(xs) {
    &__container {
      height: 180px;
    }
  }
}

.video-frame {
  width: 100%;
  max-width: 565px;
  background-color: #C4C4C4;
  position: relative;
  display: flex;
  span {
    margin: auto;
  }
  &::before {
    content: '';
    display: block;
    padding-top: 43%;
  }
}

.news-preview-block {
  &__container {
    border-radius: 2px;
    overflow: hidden;
    min-height: 220px;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-decoration: none;
    color: #fff;
    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #000;
      transition: opacity $trs1;
      opacity: 0.4;
      z-index: 2;
      will-change: opacity $trs1;
    }
    &[data-touch], &[data-hover] {
      &:before {
        opacity: 0.7;
      }
    }
  }
  &__content {
    position: relative;
    z-index: 3;
    padding: 28px 38px;
  }
  &__label {
    display: block;
    max-width: 80px;
    line-height: 24px;
    border-radius: 24px;
    color: #fff;
    text-align: center;
    font-size: 0.75rem;
    margin-bottom: 14px;
    &_style-1 {
      background-color: var(--color-label-stock, #D74853);
    }
    &_style-2 {
      background-color: var(--color-label-news, #3EC28F);
    }
  }
  @include media-breakpoint-down(sm) {
    &__content {
      padding: 28px 15px;
    }
  }
  @include media-breakpoint-down(xs) {
    &__text {
      font-size: 0.75rem;
    }
    &__label {
      margin-bottom: 12px;
    }
  }
}

.news-large-block {
  &__container {
    display: flex;
    min-height: 300px;
  }
  &__label {
    display: block;
    width: 120px;
    line-height: 40px;
    border-radius: 40px;
    color: #fff;
    text-align: center;
    font-size: 1.125rem;
    margin-bottom: 25px;
    &_style-1 {
      background-color: var(--color-label-stock, #D74853);
    }
    &_style-2 {
      background-color: var(--color-label-news, #3EC28F);
    }
  }
  &__title {
    margin: 0 0 25px 0;
  }
  &__image {
    flex: 1 1 45%;
    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #000;
      transition: opacity $trs1;
      opacity: 0.3;
      z-index: 2;
      will-change: opacity $trs1;
    }
    &[data-touch], &[data-hover] {
      &:before {
        opacity: 0.5;
      }
    }
  }
  &__content {
    flex: 1 1 55%;
    background-color: var(--color-sections-bg, #F5F5F5);
    padding: 35px 100px 35px 60px;
  }
  @include media-breakpoint-down(lg) {
    &__content {
      padding: 35px 40px;
    }
  }
  @include media-breakpoint-down(md) {
    &__content {
      padding: 35px;
      flex: 1 1 50%;
    }
    &__title {
      font-size: 1.25rem;
    }
    &__image {
      flex: 0 0 calc(50% - 10px);
    }
  }
  @include media-breakpoint-down(sm) {
    &__container {
      flex-direction: column;
      flex: 0 0 auto;
    }
    &__label {
      position: absolute;
      top: 0;
      transform: translateY(-50%);
      z-index: 5;
    }
    &__image {
      flex: 0 0 auto;
      height: 180px;
    }
    &__content {
      position: relative;
      padding: 45px 20px 30px 20px;
    }
  }
  @include media-breakpoint-down(xs) {
    &__container {
      margin-left: -16px;
      margin-right: -16px;
    }
    &__content {
      padding: 45px 16px 30px 16px;
    }
    &__title {
      font-size: 1.125rem;
      line-height: 1.5rem;
    }
    &__button {
      width: 100%;
    }
  }

}

.tabs {
  &__block {
    display: none;
  }
}

.card-price {
  &__title {
    display: none;
    color: var(--color-text-light,#bdbdbd);
    font-size: .75rem;
    line-height: 1rem;
  }
  &__label {
    display: block;
    font-size: 0.75rem;
    line-height: 1rem;
    margin-bottom: 5px;
    &_style-1 {
      color: var(--color-card-accent-1, #C43228);
    }
    &_style-2 {
      color: var(--color-card-accent-2, #4FA22E);
    }
    &_style-3 {
      color: var(--color-card-accent-3, #FF9500);
    }
    &_style-4 {
      color: var(--color-card-accent-4, #30C2FF);
    }
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__smaller-price-unit {
    display: none;
  }
  &__no-price {
    height: 1.5rem;
  }
  &__current {
    font-weight: 700;
    color: #000;
    font-size: 1.125rem;
    white-space: nowrap;
    line-height: 1.5rem;
    height: 1.5rem;
    margin-right: 8px;
    flex-shrink: 0;
    span {
      font-size: 0.8em;
    }
  }
  &__old {
    font-size: 0.75rem;
    line-height: 1rem;
    color: var(--color-text-muted, #767676);
    position: relative;
    white-space: nowrap;
    flex-shrink: 0;
    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      height: 2px;
      width: 100%;
      background-color: #D74853;
    }
  }
}

.advertising-preview {
  background-color: var(--color-border-1, #D9D9DE);
  border-radius: 2px;
  width: 100%;
  height: 380px;
  overflow: hidden;
  img {
    will-change: transform;
  }
  &[data-touch], &[data-hover] {
    img {
      transform: scale(1.05);
    }
  }
  @include media-breakpoint-down(lg) {
    height: auto;
    &:before {
      content: '';
      display: block;
      padding-top: 173%;
    }
  }
}

.popular-projects {
  &__advertising {
    padding-top: 56px;
    .advertising-preview {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }
  &__more {
    justify-content: flex-end;
  }
  &__slider {
    &_type-2 {
      .slick-slide {
        display: grid;
        grid-auto-rows: 1fr;
      }
    }
  }
  &__slider-2 {
    .slick-slide {
      display: grid;
      grid-auto-rows: 1fr;
    }
  }
  @include media-breakpoint-up(sm) {
    &__slider {
      opacity: 1;
      visibility: visible; 
    }
  }
  @include media-breakpoint-down(md) {
    &__advertising {
      display: none;
    } 
  }
}

.project-preview-card {
  &__container {
    border: 1px solid var(--color-border-1, #D9D9DE);
    border-radius: 2px;
    overflow: hidden;
    height: 100%;
    min-height: 380px;
    display: flex;
    flex-direction: column;
  }
  &__image {
    flex: 0 0 257px;
    max-height: 257px;
    position: relative;
    .image {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 1;
      &_contain {
        background-color: var(--color-border-1, #D9D9DE);
        img {
          padding: 45px;
        }
      }
    }
    &:after {
      content: '';
      display: block;
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
      pointer-events: none;
      transition: opacity $trs1;
      background: linear-gradient(0deg, rgba(35, 99, 209, 0.5), rgba(35, 99, 209, 0.5))
    }
    &[data-touch], &[data-hover] {
      &:after {
        opacity: 1;
      }
    }
  }
  &__status {
    position: absolute;
    z-index: 3;
    right: 0;
    top: 0;
    background-color: #fff;
    color: #999999;
    font-size: 0.75rem;
    font-weight: bold;
    text-align: center;
    padding: 0 5px;
    line-height: 18px;
  }
  &__index {
    position: absolute;
    z-index: 3;
    left: 0;
    bottom: 0;
    font-size: 0.75rem;
    color: #fff;
    padding: 13px;
  }
  &__content {
    flex: 1 1 100%;
    position: relative;
    padding: 10px 13px 15px 13px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: var(--color-sections-bg, #F5F5F5);
  }
  &__title {
    display: block;
    color: inherit;
    text-decoration: none;
    transition: color $trs1;
    margin-right: 120px;
    &[data-touch], &[data-hover] {
      color: var(--color-accent, #2363D1);
    }
  }
  &__rating {
    position: absolute;
    top: 0;
    right: 0;
    width: 85px;
    user-select: none;
    pointer-events: none;
  }
  &__buttons {
    display: flex;
    .button {
      &:not(:last-child) {
        margin-right: 6px;
      }
    }
  }
  &__content-bottom {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 0 0 auto;
  }
  &__content-top {
    position: relative;
    flex: 1 1 100%;
  }
  @include media-breakpoint-down(lg) {
    &__content {
      padding: 12px 12px 15px 12px;
    }
    &__content-bottom {
      flex-direction: column;
      align-items: flex-start;
      margin-top: 12px;
    }
    &__buttons {
      order: 2;
    }
    &__rating {
      top: calc(100% + 14px);
    }
    &__title {
      margin: 0;
    }
    &__buttons {
      width: 100%;
      .button {
        &:not(:last-child) {
          margin-right: 12px;
          flex: 1 1 100%;
          max-width: 100%;
        }
      }
    }
    .card-price {
      order: 1;
      margin-bottom: 15px;
    }
  }
}

.price-chart {
  &__head {
    margin-bottom: 20px;
  }
  @include media-breakpoint-up(xl) {
    &__content {
      margin: 0 -14px 0 -40px;
    }
  }
}

.specifications {
  &:not(:last-child) {
    margin-bottom: 40px;
  }
  &__title {
    margin: 0 0 20px 0;
  }
  &__table {
    width: 100%;
    span {
      background-color: #fff;
      position: relative;
      z-index: 2;
    }
    tr {
      &:not(:last-child) {
        td {
          padding-bottom: 1rem;
        }
      }
    }
    td {
      position: relative;
      vertical-align: top;
      &:first-child {
        width: 60%;
        span {
          padding-right: 1rem;
        }
        &::before {
          content: '';
          display: block;
          position: absolute;
          width: 100%;
          height: 2px;
          top: 0.875rem;
          left: 0;
          background-image: url(data:image/svg+xml;base64,);
        }
      }
      &:last-child {
        padding-left: 16px;
      }
    }
  }
  @include media-breakpoint-down(xs) {
    &__table {
      width: 100%;
      tr {
        display: block;
        width: 100%;
        &:not(:last-child) {
          margin-bottom: 15px;
        }
      }
      td {
        display: block;
        padding: 0 !important;
        &:first-child {
          color: var(--color-text-light, #BDBDBD);
          margin-bottom: 4px;
          width: 100%;
          font-size: 0.875rem;
          &::before {
            display: none;
          }
        }
        &:last-child {
          font-size: 1.125rem;
          line-height: 1.5rem;
        }
      }
    }
  }
} 


.section-partners {
  &__title {
    margin-bottom: 20px;
    text-align: center;
  }
  &__text {
    text-align: center;
    font-size: 1.125rem;
  }
  &__slider {
    margin-top: 25px;
    .slick-list {
      margin: 0 -20px;
    }
    .slider__prev,
    .slider__next {
      top: 50%;
      transform: translateY(-50%);
    } 
    .slider__prev {
      left: -95px;
    }
    .slider__next {
      right: -95px;
    }
  }
  &__slide {
    padding: 0 20px;
  }
  &__image {
    height: 52px;
  }
  @media screen and(max-width: 1439px) {
    &__slider {
        padding: 0 95px;
        .slider__next {
          right: 0;
        }
        .slider__prev {
          left: 0;
        }
      }
  }
}

.search-content-window {
  width: 100%;
  opacity: 0;
  visibility: hidden;
  position: absolute !important;
  top: 100%;
  left: 0;
  transition: opacity $trs1, visibility $trs1;
  background-color: #fff;
  border-radius: 0 0 2px 2px;
  border-color: var(--color-border-1, #D9D9DE);
  border-style: solid;
  border-width: 0 1px 1px 1px;
  max-height: 400px;
  box-shadow: 0px 2px 25px rgba(118, 118, 118, 0.15);
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__item {
    padding: 12px 20px;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &:not(:last-child) {
      border-bottom: 1px solid var(--color-border-1, #D9D9DE);
    }
    &-title {
      line-height: 1.25rem;
      max-width: 480px;
      font-size: 0.875rem;
    }
    &-value {
      font-size: 1.125rem;
      font-size: #000;
      font-weight: 700;
      display: block;
      margin-top: 10px;
    }
    &-label {
      display: inline-block;
      border-radius: 2px;
      padding: 0 7px;
      font-size: 0.75rem;
      margin-left: 3px;
      &_style-1 {
        background-color: var(--color-text-muted, #767676);
        color: #fff;
      }
      &_style-2 {
        background-color: var(--color-text-muted-2, #E5E5EA);
        color: inherit;
      }
    }
  }
}

.search {
  position: relative;
  &__form {
    display: block;
    display: flex;
    width: 100%;
    border-radius: 2px;
    background-color: #fff;
  }
  &__button {
    background-color: var(--color-button2-bg, #2363D1);
    height: 40px;
    flex: 0 0 48px;
    display: flex;
    transition: background-color $trs1;
    pointer-events: none;
    border-radius: 0 2px 2px 0;
    &[data-touch], &[data-hover] {
      background-color: var(--color-header-search-active-button-hover-bg, #1C52AC);
    }
    .icon {
      margin: auto;
      width: 20px;
      height: 20px;
      fill: #fff;
    }
  }
  &__input { 
    border-color: var(--color-border-3, #A7A7AB);
    border-width: 1px 0 1px 1px;
    border-style: solid;
    border-radius: 2px 0 0 2px;
    font-size: 0.875rem;
    flex: 1 1 100%;
    padding: 9px 15px;
    line-height: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    &::placeholder {
      color: var(--color-text-muted, #767676);
      transition: opacity $trs1;
    }
    &:focus {
      &::placeholder {
        opacity: 0 !important;
      }
    }
  }
  &.active {
    .search__button {
      pointer-events: all;
    }
  }
  &.active-content {
    .search__content {
      opacity: 1;
      visibility: visible;
    }
    .search__button {
      border-radius: 0 2px 0 0;
    }
    .search__input {
      border-radius: 2px 0 0 0;
    }
    .header-search__form {
      border-radius: 2px 2px 0 0;
    }
  }
}

.aside-categories {
  position: relative;
  z-index: 10;
  &:not(:last-child) {
    margin-bottom: 20px;
  }
  &__button {
    width: 100%;
    line-height: 46px;
    border: 1px solid var(--color-border-1, #D9D9DE);
    background-color: #fff;
    height: auto;
    color: inherit;
    position: relative;
    z-index: 2;
    justify-content: flex-start;
    padding: 0 25px;
    border-radius: 2px;
    .icon {
      width: 16px;
      height: 16px;
      margin-right: 13px;
      fill: var(--color-accent, #2363D1)
    }
    &.active {
      border-radius: 2px 2px 0 0;
    }
  }
  &__content {
    box-shadow: 0px 2px 25px rgba(118, 118, 118, 0.15);
    border-style: solid;
    border-radius: 2px;
    border-width: 1px;
    border-color: var(--color-border-1, #D9D9DE);
    background-color: #fff;
  }
  &.toggle-section {
    .aside-categories__content {
      border-width: 0 1px 1px 1px;
      border-radius: 0 0 2px 2px;
      z-index: 1;
      position: absolute;
      top: 100%;
      width: 100%;
      opacity: 0;
      visibility: hidden;
      transition: opacity $trs1, visibility $trs1;
      &.active {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__list {
    margin: 0;
    padding: 10px 0;
    list-style: none;
  }
  &__link {
    display: block;
    text-decoration: none;
    color: #000;
    line-height: 18px;
    padding: 12px 40px 12px 16px;
    position: relative;
    background-color: transparent;
    transition: background-color $trs1;
    font-size: 0.875rem;
    .icon {
      height: 14px;
      width: 14px;
      top: 14px;
      right: 16px;
      position: absolute;
      display: block;
      fill: var(--color-accent, #2363D1)
    }
    &[data-touch], &[data-hover] {
      background-color: var(--color-sections-bg, #F5F5F5);
    }
    &_style-2 {
      font-weight: 700;
      color: #000;
      font-size: 1rem;
      line-height: 1.375rem;
      .icon {
        width: 16px;
        height: 16px;
        top: 16px;
      }
    }
  }
  @include media-breakpoint-up(lg) {
    &.toggle-section {
      &.aside-categories_desktop-visible {
        .aside-categories__button {
          pointer-events: none;
          border-radius: 2px 2px 0 0;
        }
        .aside-categories__content {
          opacity: 1;
          visibility: visible;
          position: relative;
        }
      }
    }
  }
  @include media-breakpoint-down(md) {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
    &__button {
      pointer-events: all;
      border-radius: 2px;
      justify-content: center;
    }
    &__link {
      padding-right: 60px;
      .icon {
        right: 35px;
      }
    }
  }
}

.sorting-heading {
  margin-bottom: 24px;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  &__download-button {
    margin-right: 34px;
  }
  &__items {
    display: flex;
    align-items: center;
    order: 2;
  }
  &__title {
