.brigade-diagram {
  height: 200px;
  margin-top: 130px;
  position: relative;
  &__info {
    position: absolute;
    z-index: 3;
    bottom: 50%;
    &::before {
      content: '';
      display: block;
      position: absolute;
      width: 8px;
      height: 8px;
      border-radius: 8px;
      background-color: #fff;
      bottom: -4px;
      z-index: 2;
    }
    &-content {
      position: relative;
      padding-bottom: 75px;
      display: flex;
      align-items: flex-end;
      overflow: hidden;
      font-size: 1.125rem;
      line-height: 1.75rem;
    }
    span {
      color: var(--color-accent, #2363D1);
      font-size: 70px;
      line-height: 0.9;
      font-weight: 700;
      margin-right: 16px;
    }
    &-line {
      display: block;
      width: auto;
      height: 60px;
      position: absolute;
      bottom: 0;
      z-index: 1;
    }
    &_style-1 {
      right: 50%;
      .brigade-diagram__info-content {
        padding-right: 40px;
      }
      &:before {
        right: -4px;
      }
      .brigade-diagram__info-line {
        right: 0;
      }
    }
    &_style-2 {
      left: 50%;
      .brigade-diagram__info-content {
        padding-left: 40px;
      }
      &:before {
        left: -4px;
      }
      .brigade-diagram__info-line {
        left: 0;
      }
    }
  }
  &__circle {
    width: 200px;
    height: 200px;
    fill: transparent;
    position: relative;
    margin: 0 auto;
    &-item {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      &_style-1 {
        stroke: var(--color-diagram-1, #FF9500);
      }
      &_style-2 {
        stroke: var(--color-diagram-2, #2363D1);
        transform: rotate(-90deg);
      }
    }
  }
  @include media-breakpoint-down(lg) {
    margin-top: 110px;
    &__info_style-2 .brigade-diagram__info-content {
      transform: translate(-15%, 15%) scale(0.7);
    }
    &__info_style-1 .brigade-diagram__info-content {
      transform: translate(15%, 15%) scale(0.7);
    }
  }
  @include media-breakpoint-down(xs) {
    margin-top: 30px;
    margin-bottom: 65px;
    &__circle {
      width: 100%;
    }
    &__info {
      bottom: 0!important;
      position: absolute;
      top: calc(100% + 20px);
      transform: translate(0) !important;
      &::before {
        display: none;
      }
      &-line {
        display: none;
      }
      &-content {
        transform: translate(0) !important;
        padding: 0 !important;
        justify-content: center;
        align-items: center;
        font-size: 0.875rem;
        line-height: 1.25rem;
      }
      &_style-1 {
        left: 0;
        span {
          color: var(--color-diagram-1, #FF9500);
        }
      }
      &_style-2 {
        right: 0;
        span {
          color: var(--color-diagram-2, #2363D1);
        }
      }
      span {
        font-size: 48px;
        margin-right: 10px;
      }
    }
  }
}


