      max-width: 100%;
    }
  }
}

.item-slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  &__slide {
    display: block;
    width: 100%;
    &::before {
      content: '';
      display: block;
      padding-top: 100%;
    }
  }
}

.nav-slider {
  padding: 0 42px;
  &__arrow {
    width: 40px;
    height: 40px;
    display: flex;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    .icon {
      margin: auto;
      width: 16px;
      height: 16px;
      fill: var(--color-accent, #2363D1);
      transition: fill $trs1;
    }
    &-next {
      right: 0;
    }
    &-prev {
      left: 0;
    }
    &[data-touch], &[data-hover] {
      .icon {
        fill: var(--color-accent-darker, #1C52AC);
      }
    }
  }
  &__slide {
    padding: 0 8px;
    .image {
      cursor: pointer;
      border-radius: 2px;
      border: 1px solid var(--color-table-border, #E5E5EA);
      height: 48px;
      transition: border-color $trs1;
    }
    &.slick-current {
      .image {
        border-color: var(--color-accent, #2363D1);
      }
    }
  }
}




@include media-breakpoint-down(md) {
  .project-preview-card {
    &__image {
      flex: 0 0 200px;
      max-height: 200px;
    }
  }
} 
@include media-breakpoint-down(sm) {
  .logo {
    &__link {
      display: block;
      width: 160px;
    }
  }
  .header {
    &__nav-group {
      flex: 0 0 auto;
    }
  }
  .category-block {
    &__title {
      font-size: 1.125rem;
    }
  }
  .section-partners {
    &__slider {
      padding: 0 80px;
      .slick-list {
        margin: 0 -10px;
      }
    }
    &__slide {
      padding: 0 10px;
    }
  }
} 



@include media-breakpoint-down(xs) {
  .logo {
    &__link {
      width: 117px;
    }
  }
  .section-partners {
    &__title {
      margin-bottom: 15px;
    }
    &__text {
      font-size: 0.75rem;
      margin-bottom: 30px;
    }
    &__slider {
      margin-top: 30px;
    }
  }
} 

.fancybox-button {
  border-radius: 0;
  padding: 0;
  width: 40px;
  .icon {
    width: 16px;
    height: 16px;
  }
  &--arrow_left {
    transform: rotate(180deg);
  }
}

.bid-section {
  .form {
    margin-top: 25px;
  }
  &__slider {
    .image {
      height: auto;
      &:before {
        content: '';
        display: block;
        padding-top: 63%;
      }
    }
  }
  @include media-breakpoint-down(md) {
    &__form {
      margin-bottom: 30px;
    }
  }
}

.prp-section {
  &__content {
    background-color: #fff;
    height: 100%;
    padding: 40px;
  }
  @include media-breakpoint-down(md) {
    &__slider {
      display: none;
    }
  }
  @include media-breakpoint-down(xs) {
    &__content {
      padding: 0;
      background-color: transparent;
    }
  }
}

.preview-block {
  &__container {
    display: block;
    color: #fff;
    text-decoration: none;
    transition: opacity $trs1;
    &[data-touch], &[data-hover] {
      .preview-block__image:after {
        opacity: 0.2;
      }
    }
  }
  &__image {
    position: relative;
    z-index: 1;
    &::after {
      content: '';
      display: block;
      padding-top: 100%;
      position: relative;
      z-index: 2;
      background-color: #000;
      opacity: 0;
      transition: opacity $trs1;
    }
  }
  &__content {
    padding: 24px 16px;
    position: relative;
    z-index: 2;
  }
  &__label {
    display: block;
    position: absolute;
    top: 0;
    left: 16px;
    transform: translateY(-50%);
    padding: 0 10px;
    line-height: 24px;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 10px;
    color: #000;
  }
  &__title {
    color: inherit;
    font-weight: normal;
    margin: 0;
  }
}

.personal-form {
  &__title {
    margin-top: 0;
  }
  &__text {
    font-size: 1rem;
    line-height: inherit;
  }
  @include media-breakpoint-up(lg) {
    &__text {
      font-size: 1.125rem;
    }
  }
  @include media-breakpoint-down(lg) {
    &__text {
      margin-bottom: 28px;
    }
  }
}

.section-text-image {
  &__image {
    .image {
      min-height: 260px;
      height: 100%;
    }
  }
  @include media-breakpoint-down(lg) {
    &__content {
      margin-bottom: 30px;
    }
  }
  @include media-breakpoint-down(xs) {
    .image {
      margin: 0 -16px;
    }
  }
}

.landing-banner {
  min-height: 480px;
  height: auto !important;
  .slick-dots {
    bottom: 34px !important;
  }
  &__slide {
    position: relative;
    .container {
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;
      z-index: 2;
      padding-top: 100px;
      padding-bottom: 100px;
    }
    .image {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
  &__form {
    color: #fff;
    position: relative;
    z-index: 2;
    max-width: 420px;
    margin: 0 0 0 auto;
    &-title {
      color: inherit;
      margin: 0 0 20px 0;
      font-size: 42px;
      line-height: 1;
    }
    &-subtitle {
      color: inherit;
      margin: 0;
      font-size: 24px;
      line-height: 1.25;
    }
    &-subscription {
      margin: 20px 0 0 0;
    }
  }
  @include media-breakpoint-down(xs) {
    min-height: 400px;
    &__form {
      text-align: center;
      padding: 0 16px;
      &-title {
        font-size: 1.375rem;
        line-height: 1.75rem;
        padding: 0 16px;
      }
      &-subtitle {
        padding: 0 16px;
        font-size: 18px;
      }
    }
  }
}

.wide-project-card {
  &__container {
    height: 100%;
    border: 1px solid var(--color-border-1, #D9D9DE);
    border-radius: 2px;
    background-color: #fff;
    min-height: 180px;
    display: flex;
  }
  &__image {
    flex: 0 0 320px;
    position: relative;
    z-index: 1;
    .image {
      width: 100%;
      height: 100%;
    }
    &:after {
      content: '';
      display: block;
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
      pointer-events: none;
      transition: opacity $trs1;
      background: linear-gradient(0deg, rgba(35, 99, 209, 0.5), rgba(35, 99, 209, 0.5))
    }
    &[data-touch], &[data-hover] {
      &:after {
        opacity: 1;
      }
    }
  }
  &__content {
    padding: 20px;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }
  &__index {
    display: block;
    position: absolute;
    right: 10px;
    top: 10px;
    color: var(--color-text-light, #BDBDBD);
    font-size: 0.75rem;
  }
  &__title {
    margin-top: 0;
  }
  &__text {
    margin-bottom: 15px;
  }
  &__bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: -15px;
  }
  &__calc {
    max-width: 65px;
    .input__element {
      padding: 0;
      line-height: 20px;
    }
  }
  &__button, &__calc, &__price {
    margin-bottom: 15px;
  }
  @include media-breakpoint-down(md) {
    &__image {
      flex: 0 0 332px;
    }
  }
  @include media-breakpoint-down(sm) {
    &__container {
      flex-direction: column;
    }
    &__image {
      flex: 0 0 200px;
    }
    &__content {
      padding: 12px 12px 15px 12px;
    }
    &__index {
      padding: 13px;
      left: 0;
      top: 0;
      right: initial;
      transform: translateY(-100%);
      color: #fff;
    }
    &__title {
      margin-bottom: 10px;
    }
    &__text {
      margin-bottom: 12px;
    }
    &__price {
      flex: 1 1 100%;
    }
    &__button {
      flex: 1 1 auto;
    }
    &__calc {
      flex: 0 0 auto;
      margin-right: 12px;
    }
  }
}

.faq-element {
  position: relative;
  background-color: #f5f5f5;
  &:not(:last-child) {
    margin-bottom: 15px;
  }
  &__head {
    padding: 15px 70px 15px 20px;
    min-height: 60px;
    display: flex;
    align-items: center;
    position: relative;
    user-select: none;
    cursor: pointer;
    &[data-touch], &[data-hover] {
      .faq-element__trigger {
        background-color: var(--color-button1-hover-bg, #E7EEF6);
        border-color: var(--color-button1-hover-border, #2363D1);
      }
    }
    &.active {
      .faq-element__trigger {
        .icon {
          transform: rotate(180deg);
        }
      }
    }
  }
  &__head-title {
    margin: 0;
  }
  &__trigger {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 50%;
    border: 1px solid var(--color-button1-border, #B7CBE5);
    background-color: var(--color-button1-bg, transparent);
    transition: border-color $trs1, background-color $trs1;
    .icon {
      fill: var(--color-button1-icon, #2363D1);
      width: 14px;
      height: 14px;
      transition: transform $trs1;
    }
  }
  &__content {
    padding: 5px 20px 20px 20px;
  }
  @include media-breakpoint-down(xs) {
    &__content {
      padding: 5px 15px 15px 15px;
    }
    &__head {
      padding: 15px 70px 15px 15px;
    }
    &__head-title {
      font-size: 1rem;
      line-height: 1.3;
    }
  }
}

.product-slider {
  &_rows-2, &_rows-3 {
    .slick-slide {
      display: grid;
      grid-auto-rows: 1fr;
    }
  }
}